import React from 'react'
import {
  Drawer,TextField, Typography,Button,Grid,MenuItem,Select,InputLabel,FormControl
} from "@material-ui/core";
import { makeStyles, createStyles } from "@material-ui/core/styles";
import { useTheme } from "@material-ui/styles";
import VNDInput from '../../../../components/TextField/NumberFormatCustom'
import { useSelector } from 'react-redux';
import { useFormik } from 'formik';
import moment from 'moment'
import refundApi from '../../../../api/refundApi';

const drawerWidth = 300;
const useStyles = makeStyles((theme) =>
  createStyles({
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
    },
    
    drawerPaper: {
      width: drawerWidth,
      padding:15
    },
    textField:{marginBottom:10},
    text:{marginBottom:10,marginTop:15}
  })
);
const InvoiceReturnFilter = (props) => {
  const {handleToggleFilter,openFilter, setRefunds} =props;

  const theme = useTheme();
  const classes = useStyles(theme);

  const info = useSelector((state) => state.info);
  const store_uuid = info.store.uuid;
  const branch_uuid = info.branch.uuid;

  const formik = useFormik({
    initialValues: {
      startDate: '',
      endDate: '',
      orderCode: '',
      minTotalAmount: 0,
      maxTotalAmount: 0,
      status: 'debt',
      paymentMethod: '',
    },
    onSubmit: async values => {
      console.log(values)
      const res = await refundApi.searchRefund(store_uuid, branch_uuid, values)
      setRefunds(res.data)
    },
  });

  return (
    <Drawer
      anchor="right"
      onClose={handleToggleFilter}
      open={openFilter}
      classes={{
        paper: classes.drawerPaper,
      }}
      className={classes.drawer}
    >
      {/* Nhan vien, khach hang?? search ở đây hay chỗ kia -> search sđt ,mã , tên,...*/}
      {/* Giao hàng, dia chi giao ???*/}
      {/* tiền giảm ???*/}


      {/* 1.Ngay from-to */}
      <Typography variant="h5" className={classes.text} >Mã hoá đơn:</Typography>
      <TextField 
        id="orderCode" label="Mã hoá đơn" type="text" name="orderCode"
        defaultValue={formik.values.orderCode} 
        variant="outlined" size="small" 
        fullWidth className={classes.textField} 
        InputLabelProps={{ shrink: true }} 
        value={formik.values.orderCode}  
        onChange={formik.handleChange}
      />

      <Typography variant="h5" className={classes.text} >Ngày tạo:</Typography>
      <TextField id="startDate" label="Từ" 
        type="date" 
        name="startDate"
        defaultValue={formik.values.startDate} 
        variant="outlined" size="small" fullWidth 
        className={classes.textField} 
        InputLabelProps={{ shrink: true }} 
        value={formik.values.startDate} 
        onChange={formik.handleChange}
      />
      <TextField 
        id="endDate" label="Đến" type="date" name="endDate"
        defaultValue={formik.values.endDate} 
        variant="outlined" size="small" 
        fullWidth className={classes.textField} 
        InputLabelProps={{ shrink: true }} 
        value={formik.values.endDate}  
        onChange={formik.handleChange}
      />
     

      {/* 2.Tien from-to*/}
      <Typography variant="h5" className={classes.text} >Tiền đơn trả:</Typography>
      <VNDInput required label="Từ" variant="outlined" fullWidth size="small" className={classes.textField}
        name="minTotalAmount"
        value={formik.values.minTotalAmount}
        onChange={formik.handleChange}
        value={formik.values.minTotalAmount}
      // onChange={(e) =>
      //   setProductInfo({
      //     ...productInfo,
      //     importedPrice: e.target.value,
      //   })
      // }
      />
      <VNDInput 
        id="maxTotalAmount" name="maxTotalAmount"
        required label="Đến" variant="outlined" 
        fullWidth size="small" className={classes.textField}
        value={formik.values.maxTotalAmount}
        onChange={formik.handleChange}
      // onChange={(e) =>
      //   setProductInfo({
      //     ...productInfo,
      //     importedPrice: e.target.value,
      //   })
      // }
      />

      



      {/* 4.Chi nhanh */}
      {/* <Typography variant="h5"className={classes.text} >Chi nhánh:</Typography>
      <FormControl
          className={classes.formControl}
          fullWidth
          size="small"
          variant="outlined"
        >
      <Select
        labelId="demo-simple-select-outlined-label"
        id="demo-simple-select-outlined"
        size="small"
        // onChange={(event) => { setGender(event.target.value) }}
        // label=" Chi nhánh"
        // value={gender}
      >
        <MenuItem value="trungtam">Chi nhánh trung tâm</MenuItem>
        <MenuItem value="q1">Chi nhánh quận 1</MenuItem>
      </Select>
      </FormControl> */}



      {/* 5.Trang thai */}
      <Typography variant="h5" className={classes.text} >Trạng thái:</Typography>
      <FormControl
        className={classes.formControl}
        fullWidth
        size="small"
        variant="outlined"
      >
        <Select
          labelId="demo-simple-select-outlined-label"
          id="status"
          name="status"
          size="small"
          onChange={formik.handleChange}
          // label=" Chi nhánh"
          value={formik.values.status}
        >
          <MenuItem value="debt">Còn nợ</MenuItem>
          <MenuItem value="closed">Trả đủ</MenuItem>
        </Select>
      </FormControl>



      {/* 5.Pttt */}
      <Typography variant="h5" className={classes.text} >Hình thức trả:</Typography>
      <FormControl
        className={classes.formControl}
        fullWidth
        size="small"
        variant="outlined"
      >
        <Select
          labelId="demo-simple-select-outlined-label"
          id="paymentMethod" name="paymentMethod"
          size="small"
          onChange={formik.handleChange}
          // label=" Chi nhánh"
          value={formik.values.paymentMethod}
        >
          <MenuItem value="card">Thẻ</MenuItem>
          <MenuItem value="cash">Tiền mặt</MenuItem>
        </Select>
      </FormControl>

      {/* BUTTON */}
      <Button
        variant="contained"
        color="primary"
        onClick={formik.handleSubmit}
        style={{ marginTop: 30 }}>
        Lọc
      </Button>



    </Drawer>
  )
}

export default InvoiceReturnFilter

